import {
	getApplyChildOrgList,
	getApplyAffairList
} from '@/api/declare.js'
import store from '@/store'
import msDropdownMenu from '@/components/ms-dropdown/dropdown-menu.vue'
import msDropdownItem from '@/components/ms-dropdown/dropdown-item.vue'

var _self, page = 1,
	pagesize = 10,
	count = 0,
	timer = null;

export default {
	components: {
		msDropdownMenu,
		msDropdownItem
	},
	data() {
		return {
			nav: [{
					name: "已提交",
					step: 1,
					sts: "10",
				},
				{
					name: "一级审批",
					step: 2,
					sts: "100",
				},
				{
					name: "二级审批",
					step: 3,
					sts: "200",
				},
				{
					name: "完成",
					step: 4,
					sts: "200",
				}
			],
			applyList: [],
			applyid: 0,
			stsList: [{
					text: '全部状态',
					value: -1
				},
				{
					text: '未提交',
					value: 1
				},
				{
					text: '一级审批',
					value: 10
				},
				{
					text: '二级审批',
					value: 100
				},
				{
					text: '申报完成',
					value: 200
				}
			],
			sts: -1,
			newsList: [],
			loadingType: 'more', //定义加载方式 0---contentdown  1---contentrefresh 2--contentnomore

		}

	},
	onLoad: function(option) {
		if (option.id != undefined) {
			this.applyid = option.id
		}
		//页面一加载时请求一次数据
		// this.TowerSwiper('swiperList');
		_self = this;
		uni.showLoading({
			title: "加载中..."
		})
		getApplyAffairList({
			token_wx: store.getters.token,
			p: 1,
			ps: 10000,
			keywords: ""
		}).then(res => {
			var data = [{
				text: '全部项目',
				value: 0
			}];
			if (res.data.count != 0) {
				// this.applyid = res.data[0].id
				res.data.forEach(item => {
					data.push({
						text: item.name,
						value: item.id
					})
				})
				this.applyList = data;
			}

			_self.getnewsList();
			uni.hideLoading()
		})
	},
	onPullDownRefresh: function() {
		//下拉刷新的时候请求一次数据
		_self.getnewsList();
	},
	onReachBottom: function() {
		console.log(222)
		//触底的时候请求数据，即为上拉加载更多 
		_self.getmorenews();
	},
	methods: {
		getSts: function(doc) {
			var sts = '待审批';
			if (doc.sts == 10) {
				sts = '待一级审批';
			} else if (doc.sts == 100) {
				sts = '待二级审批';
			} else if (doc.sts == 200) {
				sts = '已通过';
			} else if (doc.sts == -500) {
				sts = '未通过';
			} else if (doc.is == 1 && doc.sts == 1) {
				sts = '已驳回';
			}
			return sts
		},
		// getApplyList() {
		// 	getApplyAffairList({}).then(res => {

		// 	})
		// },
		// 上拉加载
		getmorenews: function() {
			if (_self.loadingType !== "more") { //loadingType!=0;直接返回
				return false;
			}
			if (count <= _self.newsList.length) {
				_self.loadingType = "noMore";
				return false;
			}
			_self.loadingType = "loading";
			uni.showNavigationBarLoading(); //显示加载动画 
			getApplyChildOrgList({
				token_wx: store.getters.token,
				p: page,
				ps: pagesize,
				applyid: this.applyid,
				sts: this.sts,
				keywords: ""
			}).then(res => {
				if (res.data.length == 0) { //没有数据
					_self.loadingType = "noMore";
					uni.hideNavigationBarLoading(); //关闭加载动画
					return;
				}
				page++; //每触底一次 page +1
				_self.newsList = _self.newsList.concat(res.data); //将数据拼接在一起
				_self.loadingType = "more"; //将loadingType归0重置
				uni.hideNavigationBarLoading(); //关闭加载动画 
			})
		},
		// 下拉刷新
		getnewsList: function() { //第一次回去数据
			page = 1;
			this.loadingType = "more";
			uni.showNavigationBarLoading();
			getApplyChildOrgList({
				token_wx: store.getters.token,
				p: page,
				ps: pagesize,
				applyid: this.applyid,
				sts: this.sts,
				keywords: ""
			}).then(res => {
				count = res.count;
				if (count == 0 || count <= pagesize) { //没有数据
					_self.loadingType = "noMore";
				}
				page++; //得到数据之后page+1
				_self.newsList = res.data;
				// console.log(_self.newsList)
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh(); //得到数据后停止下拉刷新 
			})
		},


	}
}
